.users {
	color: #e4e4e4;
	padding: 1rem;
	display: flex;
	flex-direction: column;
	gap: 1rem;
}

.users__item {
	padding: 1rem;

	display: flex;
	align-items: center;

	border-bottom: 1px solid darkslateblue;
}

.users p {
	margin: 0 0 1rem 0;
}

.users__name {
	display: flex;
	flex-direction: column;
	align-items: center;

	font-size: 24px;

	flex-grow: 1;

	max-width: 35%;
}

.users__name a {
	text-decoration: none;
	color: inherit;
}

.users__name img {
	margin: 0 auto;
	width: 150px;
	height: 150px;

	border-radius: 50%;
}

.users__info {
	margin-left: 5rem;
}